@use "base/variables" as v;
@use "base/mixins" as m;

.error-mensaje {
    width: 100%;
    max-height: 0;
    background-color: v.$rosa-bebe;
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 0;
    padding: 0;
    border-radius: 2rem;
    color: v.$blanco;
    opacity: 0;
    transform: translateY(-1rem);
    transition: all .3s ease;
}

.error-mensaje.visible {
    max-height: fit-content;
    transform: translateY(0);
    opacity: 1;
    margin-bottom: 2rem;
    padding: .5rem 0;
    transition: all .3s ease;
}

.error-global {
    margin: 0 auto 5rem auto;
    padding: 0 1rem;
    display: flex;
    align-items: center !important;
    justify-content: center !important;
    width: 90%;
    text-align: center;
}

.cargando-fechas, .sin-fechas {
    text-align: center;
    font-family: v.$fuente_titulo;
    color: v.$gris;
    text-transform: uppercase;
    font-weight: 700 !important;
}

/* Modal de error personalizado */
.modal-error {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    z-index: 1000;
    max-width: 90%;
    width: 400px;
    text-align: center;
    animation: fadeIn 0.3s ease-out;
    border: 1px solid v.$beige;

    h3 {
        color: v.$marron;
        margin-bottom: 1rem;
        font-size: 1.25rem;
    }

    p {
        margin-bottom: 1.5rem;
        color: #333;
        line-height: 1.5;

        strong {
            font-weight: 700;
        }
    }

    button {
        background-color: v.$gris;
        color: v.$blanco;
        border: none;
        padding: 0.5rem 1.5rem;
        border-radius: 4px;
        cursor: pointer;
        font-weight: 500;
        transition: background-color 0.2s;

        &:hover {
            background-color: v.$beige;
        }
    }
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
    animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}



