@use "../base/variables" as v;
@use "../base/mixins" as m;

nav {
    position: relative;
    background-color: v.$negro;
    color: v.$crema-blanco;
    text-align: center;
    font-size: 1rem;
    margin-top: 0;
    padding: .5rem 0;

    h1 {
        margin: 0;
    }

    button, .usuario-nombre {
        position: absolute;
        top: .35rem;
        right: 1rem;
    }

    .usuario-nombre {
        top: .7rem;
        font-size: 1.2rem;
        font-weight: 500;
    }
}

.barra-progreso {
    width: 80%;
    margin: 2rem auto 0 auto;

    .progreso {
        margin: 0 auto;
        width: 20%;
        height: .5rem;
        background-color: v.$negro;
        border-radius: 1rem;
        transition: .3s ease;
    }
}

.contenedor-maqueta {
    width: 100%;
    margin: 0 auto;
    padding: 2rem;
    background: v.$crema-blanco;
    border-radius: 1rem;
    transition: all .3s ease;
    
    @include m.tablet {
        max-width: 60rem;
    }
}

.paso {
    display: none;
    max-height: calc(100vh - 20rem);
    overflow-y: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;

    @include m.desktop {
        margin: 0 auto;
        // width: 60%;
    }

    &::-webkit-scrollbar {
        display: none;
    }

    &.activo {
        display: block;
    }

    &.paso-6 {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .sin-horarios, .cargando-horarios {
        text-align: center;
    }
}

.contenedor {
    width: 80%;

    .titulo, .subtitulo {
        text-align: center;
    }

    .titulo {
        font-size: 3rem;
        margin-bottom: .2rem;
    }

    .subtitulo {
        color: v.$gris;
        text-transform: uppercase;
        margin-bottom: 2rem;
    }

    .flotante {
        position: relative;
        background-color: v.$blanco;
        border-radius: 2rem;
        margin-bottom: 1rem;
        padding: 1rem;
        cursor: pointer;
        transition: all 0.3s ease;
        border: 1px solid transparent; // Borde transparente por defecto
        -webkit-tap-highlight-color: transparent;
        user-select: none;

        &:not(.seleccionado):hover {
            transform: translateY(-2px);
            box-shadow: 0 4px 8px rgba(0,0,0,0.05);
            transition: all 0.3s ease;
        }

        .check-icon {
            position: absolute;
            right: 1.5rem;
            top: 50%;
            transform: translateY(-50%) scale(0);
            width: 1.8rem;
            height: 1.8rem;
            fill: v.$gris;
            opacity: 0;
            transition: opacity 0.2s, transform 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
        }

        &.seleccionado {
            border-color: v.$gris; // Tu color de borde original
            transition: all .3s ease;

            .check-icon {
                opacity: 1;
                transform: translateY(-50%) scale(1);
                transition-delay: .2s;
            }
        }

        &.servicio {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 2rem;

            // .detalles-servicio {
            //     display: flex;
            //     flex-direction: column;

            //     span {
            //         font-size: 1.2rem;
            //         text-align: right;
            //         color: #666666;
            //     }
            // }
        }

        &.horario {

            .p1 {
                width: 100%;
                text-align: center;
            }

            &.ocupado {
                opacity: .5;
                cursor: not-allowed;
                pointer-events: none;
                filter: grayscale(70%);
            }

            &.no-disponible {
                display: none;
            }
        }

        &.servicio p,
        &.horario p {
            color: v.$negro;
            text-align: center;
        }

        &.fecha {

            .p1 {
                width: 100%;
                text-align: center;
                color: v.$negro;
            }
        }

        .p1, .p2 {
            font-size: 1.4rem;
        }

        .p1 {
            text-transform: uppercase;
            color: v.$gris;
        }
    }

    .form-datos {
    max-width: 500px;
    margin: 0 auto;

        .grupo-form {
            position: relative;
            width: 100%;
            padding: 1rem;
            padding-top: 2.5rem;
            background-color: transparent;

            &:not(.grupo-entrega) {
                input:not([type="radio"]) {
                width: 100%;
                border: 0;
                border-bottom: 1px solid v.$gris;
                outline: none;
                background: transparent;
                font-size: 1.5rem;
                color: v.$negro;
                letter-spacing: 1px;
                padding: 10px 0;
                transition: 0.3s;
                }
            }

            label {
                position: absolute;
                display: flex !important;
                top: 3rem; /* Ajustado para alineación inicial */
                left: 1rem;
                font-size: 1.5rem;
                letter-spacing: 1px;
                color: v.$marron;
                transition: 0.3s ease all;
                pointer-events: none;
                transform-origin: top left;
                background-color: transparent;
            }

            small {
                font-size: 1rem;
                color: v.$marron;
            }

            input:focus + label,
            input:not(:placeholder-shown) + label,
            input.has-content + label {
                transform: translateY(-2.5rem) scale(0.8); /* Efecto más suave con scale */
                color: #8b6f32; /* Color diferente cuando está arriba */
            }

            textarea.input-floating {
                width: 100%;
                border: 0;
                border-bottom: 1px solid #aaaaaa;
                outline: none;
                background: transparent;
                font-size: 1.5rem;
                font-family: v.$fuente_principal;
                letter-spacing: .1rem;
                padding: 10px 0;
                transition: 0.3s;
                color: v.$negro;
                min-height: 60px; /* Altura mínima inicial */
            }

            /* Efecto flotante para textarea (igual que inputs) */
            textarea:focus + label,
            textarea:not(:placeholder-shown) + label,
            textarea.has-content + label {
                transform: translateY(-2.5rem) scale(0.8);
                color: #8b6f32;
            }
            
            .small-mensaje {
                color: v.$marron;
                font-size: 0.8rem;
            }

            .invalido + label + .small-mensaje {
                color: #e91b1b;
            }
        }
    }

    .resumen {
        margin-top: 3rem;
        box-shadow: 0 6px 8px rgba(0, 0, 0, 0.185);

        .datos-turno {
            display: flex;
            width: 100%;
            border-bottom: solid .1rem #dbdbdb;
            margin-bottom: 1rem;

            @include m.telefono {
                width: 80%;
                margin: 0 auto;
            }

            &:nth-child(3) {
                border-bottom: none;
            }

            .titulo-turno {
                width: 30%;

                h4 {
                    width: 100%;
                    margin: 0 1rem 0;
                    text-align: left;
                    color: #777;
                }
            }

            .descripcion-turno {
                display: flex;
                flex-direction: column;
                gap: .3rem;
                width: 70%;

                p {
                    text-align: right;

                    span {
                        font-weight: 400;
                        color: #333;

                        &#subtotal, &#senia {
                            font-weight: bold;
                        }

                        &#saldo-pendiente {
                            display: none;
                        }
                    }
                }
            }
        }

        .fecha-hora {
            display: flex;
        }

        p {
            margin-bottom: 0.5rem;
            font-weight: normal;
            text-align: center;

            &#mail-cliente {
                display: none;
            }

            span {
                // text-transform: uppercase;
                font-size: 1.5rem;
                font-weight: 600;
                color: #292724;
            }
        }
    }

    .exito, .error {
        display: none;
        color: v.$marron;

        &.visible {
            display: block;
        }

        .confirmacion-icono {
            width: 60px;
            height: 60px;
            margin-top: 2rem;
            display: block;
            place-self: center;
        }
    }

    .exito {

        h2, p {
            text-align: center;
        }

        h2 {
            font-size: 2.5rem;
            margin-bottom: 1rem;
        }

        p {
            font-size: 1.3rem;
            font-weight: 600;
        }

        .confirmacion-icono {
            margin-top: 2rem;
            display: block;
            place-self: center;
            fill: #8fa77a;
        }

        .resumen-confirmacion {
            background-color: v.$blanco;
            padding: .5rem 1rem;
            margin: 1.5rem 0;
            border-radius: 2rem;
            text-align: left;

            h3 {
                font-size: 2rem;
                margin-bottom: 1rem;
                text-align: center;
            }

            p {
                margin: .5rem 0;
                font-size: 1.5rem;
                font-weight: 300;

                &.total-confirmacion {
                    font-weight: 700;
                    margin-top: 1rem !important;
                }
            }
        }
    }

    .error {

        .confirmacion-icono {
            fill: #85423f;
        }
    }
}

/* Botón */
#nueva-reserva {
  background-color: v.$blanco;
  color: v.$marron;
  border: solid .1rem v.$marron;
  margin: 0 auto;
  padding: 0.8rem 2rem;
  border-radius: 1.2rem;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  margin-top: 1rem;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

#nueva-reserva:hover {
  background-color: v.$marron;
  transform: translateY(-2px);
  color: v.$blanco;
}

#nueva-reserva svg {
  width: 18px;
  height: 18px;
  fill: v.$rosa-bebe;
}